footer {
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	text-align: center;
}

.topnav {
	overflow: hidden;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	gap: 1.2mm;
}

.topnavBtn {
	width: min-content !important;
	font-weight: 510;
	color: black;
}

.topnavImg {
	float: left;
}

.blogLink {
	color: blue !important;
}

.blogPrev {
	overflow: hidden;
	display: -webkit-box;
	-webkit-line-clamp: 2;
	line-clamp: 2;
	-webkit-box-orient: vertical;
	font-size: 10pt !important;
}

.blogSep {
	overflow: hidden;
	display: -webkit-box;
	-webkit-line-clamp: 1;
	line-clamp: 1;
	-webkit-box-orient: vertical;
}